<template>
    <div>
        <div class="person_top">
            <div style="height: 89px"></div>
            <div class="person_top_avatar">
                <!--头像-->
                <div class="avatar" @click="avatar">
                    <van-image
                            round
                            width="5rem"
                            height="5rem"
                            :src="tx_img"
                    />
                </div>
                <!--立即登录-->
                <div class="avatar_text">
                    <div style="height: 20px"></div>
                    <div class="login">
                        <p class="immediately_login" @click="immediatelyLogin" v-if="isRes">立即登录</p>
                        <p class="immediately_login" v-else>{{username}}</p>
                        <p class="immediately_res" @click="immediatelyregster">立即注册</p>
                        <p class="integral">积分：{{integral}}</p>
                    </div>
                </div>
            </div>
        </div>
        <van-cell-group>
            <van-cell title="每天一个好心情" class="slogan"/>
            <van-cell is-link icon="notes-o" @click="today_clockin">
                <!-- 使用 title 插槽来自定义标题 -->
                <template #title>
                    <span class="custom-title">每日打卡</span>
                </template>
            </van-cell>
            <van-cell is-link icon="underway-o" @click="record">
                <!-- 使用 title 插槽来自定义标题 -->
                <template #title>
                    <span class="custom-title">打卡记录</span>
                </template>
            </van-cell>

            <van-cell is-link icon="records" @click="article">
                <!-- 使用 title 插槽来自定义标题 -->
                <template #title>
                    <span class="custom-title">发布文章</span>
                </template>
            </van-cell>
            <van-cell is-link icon="description" @click="personArticle">
                <!-- 使用 title 插槽来自定义标题 -->
                <template #title>
                    <span class="custom-title">我的文章</span>
                </template>
            </van-cell>
        </van-cell-group>
        <div style="margin: 16px;">
            <van-button round block type="info" native-type="submit" @click='out'>
                退出
            </van-button>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Personfile",
        data(){
            return{
                integral:0,
                isRes:true,
                username:'',
                tx_img:require('../../assets/img/personfile/userimg.png')
            }
        },
        created(){
            let name=this.$getCookie('username')
            if(name){
                this.username= name
                this.isRes=false
                this.integral=this.$getCookie(' integral')
                this.tx_img=this.$getCookie(' tx_img')
            }else {
                this.isRes=true
            }
        },
        methods:{
            immediatelyLogin(){
                this.$router.push({
                    path:'/login'
                })
            },
            immediatelyregster(){
                this.$router.push({
                    path:'/register'
                })
            },
            avatar(){
                if(this.$getCookie('username')){
                    this.$router.push('/info')
                }else {
                    this.$toast.fail('请先登录')
                }
            },
            today_clockin(){
                if(this.$getCookie('username')){
                    this.$router.push('/clockin')
                }else {
                    this.$toast.fail('请先登录')
                }
            },
            record(){
                if(this.$getCookie('username')){
                    this.$router.push('/record')
                }else {
                    this.$toast.fail('请先登录')
                }
            },
            article(){
                if(this.$getCookie('username')){
                    this.$router.push('/article')
                }else {
                    this.$toast.fail('请先登录')
                }
            },
            personArticle(){
                if(this.$getCookie('username')){
                    this.$router.push('/person_article')
                }else {
                    this.$toast.fail('请先登录')
                }
            },
            out(){
                this.$removeCookie(' username')
                this.$removeCookie(' email')
                this.$removeCookie(' tx_img')
                this.$removeCookie(' integral')
                this.isRes=true
                this.integral=0
                this.tx_img=require('../../assets/img/personfile/userimg.png')
            }
        },
    }
</script>

<style scoped>
   .person_top{
       width: 100%;
       height: 198px;
       background-color: rgb(146,188,255);
   }
    .person_top_avatar{
        width: 100%;
        height: 109px;
    }
    .avatar{
        width: 84px;
        height: 84px;
        margin-left: 16px;
        float: left;
        border: 1px;
    }
    .avatar_text{
        float: left;
        margin-left: 17px;
        height: 84px;
    }
    .immediately_login{
        color: #fff;
        font-size: 17px;
        line-height: 21px;
        height: 21px;
        margin: 0;
    }
   .immediately_res{
       color: darkred;
       font-size: 12px;
       line-height: 21px;
       height: 21px;
       margin: 0;
   }
    .slogan{
        color: #ccc;
        font-size: 12px;
    }
    .integral{
        font-size: 13px;
        height: 13px;
        line-height: 13px;
        color: #fff;
    }
    .van-cell--clickable{
        text-align: left;
    }
</style>